@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
* {
  margin: 0;
}

:root {
  font-size: 16px;
}

main {
  margin-left: 5rem;
  padding: 1rem;
}

.navbar {
  position: fixed;
  background-color: black;
  transition: width 0.2s ease;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.nav-item {
  width: 100%;
}

.nav-item:last-child {
  margin-top: auto;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: rgb(188, 17, 204);
  text-decoration: none;
  -webkit-filter: grayscale(90%) opacity(0.7);
          filter: grayscale(90%) opacity(0.7);
  transition: 0.6s;
}

.nav-link:hover {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
  background: #23232e;
}

.link-text {
  margin-left: 1rem;
  display: none;
  color: white;
}

.nav-link:hover .link-text {
  background: #23232e;
  color: white;
  transition: 0.6s;
}

.nav-link svg {
  max-width: 2rem;
  margin: 0 1.5rem;
}

.fa-primary {
  color: #ff7eee;
}

.fa-secondary {
  color: #df49a6;
}

.fa-primary,
.fa-secondary {
  transition: 0.6s;
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  padding-left: 0.5rem;
  margin-bottom: 1rem;
  text-align: center;
  color: #ececec;
  background: black;
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  transform: rotate(0deg);
  transition: transform 0.6s;
}

.navbar:hover .logo svg {
  transform: rotate(-180deg);
}

.white {
  background: #fff;
  color: black;
}

@media only screen and (min-width: 600px) {
  .navbar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .navbar:hover {
    width: 13rem;
  }
  .navbar:hover .link-text {
    display: block;
    transition: opacity 0.6s;
  }
}

@media only screen and (max-width: 600px) {
  .navbar {
    bottom: 0;
    width: 100vw;
    height: 5rem;
  }

  .logo {
    display: none;
  }
  .navbar-nav {
    flex-direction: row;
  }
  .nav-link {
    justify-content: center;
  }
  main {
    margin: 0;
  }
}

